import React from "react";
import {
  MainContainer,
  Number,
  TextContainer,
  Text,
  IconItemContainer,
  SubText,
} from "./Steps.styles";
function Steps() {
  return (
    <MainContainer>
      <IconItemContainer>
        <Number>1</Number>
        <TextContainer>
          <Text>CADASTRO</Text>
          <SubText>Faça seu cadastro na plataforma em poucos cliques.</SubText>
        </TextContainer>
      </IconItemContainer>
      <IconItemContainer>
        <Number>2</Number>
        <TextContainer>
          <Text>TORNEIOS</Text>
          <SubText>
            Escolha o torneio com as regras que mais lhe agradar e opere pelo
            navegador na plataforma da Arena
          </SubText>
        </TextContainer>
      </IconItemContainer>
      <IconItemContainer>
        <Number>3</Number>
        <TextContainer>
          <Text>RANKING</Text>
          <SubText>
            Veja o resultado e estatísticas de todos os participantes do torneio
            e compare suas estratégias
          </SubText>
        </TextContainer>
      </IconItemContainer>
      <IconItemContainer>
        <Number>4</Number>
        <TextContainer>
          <Text>SAQUE</Text>
          <SubText>
            Resgate instantaneamente os prêmios recebidos ao final do torneio
            via PIX
          </SubText>
        </TextContainer>
      </IconItemContainer>
    </MainContainer>
  );
}

export default Steps;
