import React, { Component } from "react";
import Header from "../../components/Header/Header";
import Footer from "../../components/Footer/Footer";
import SubHeaderCarousel from "../../components/SubHeaderCarousel/SubHeaderCarousel";
import IconList from "../../components/IconList/IconList";
import Steps from "../../components/Steps/Steps";
import About from "../../components/About/About";
import CandleGraph from "../../assets/images/CandleGraph.png";
import HomeMockup from "../../assets/images/HomeMockup.png";
import RankingMockup from "../../assets/images/RankingMockup.png";
import ModalityA from "../../assets/images/ModalityA.png";
import ModalityB from "../../assets/images/ModalityB.png";
import {
  TextBold,
  TextUpperCase,
  Text,
  TextBoldUppercase,
  MainContainer,
  GradientBackground,
  Image,
  CandleGraphContainer,
  CandleGraphText,
  OrangeText,
  WhiteText,
  BlackText,
  BlueText,
  GreenText,
  RankingContainer,
  RankingContainerContent,
  RankingContainerText,
  RankingContainerTitle,
  RankingImage,
  ModalityContainer,
  ModalityContainerTitle,
  ModalityContent,
  ModalityImage,
  ModalityRow,
  ModalityTitle,
  ModalityText,
  HomeMockupImage,
  ModalityContainerContent,
  StepContainer,
  StepTitle,
  StepButton,
} from "./Index.styles";

class Index extends Component {
  render() {
    return (
      <MainContainer>
        <Header />
        <SubHeaderCarousel />
        <GradientBackground>
          <CandleGraphContainer>
            <CandleGraphText>
              <TextBoldUppercase>
                NA ARENA, TODOS OS TORNEISO SÃO PERSONALIZADOS.
              </TextBoldUppercase>
              <TextUpperCase>
                <OrangeText>ESCOLHA O QUE MAIS TE AGRADAR E PRONTO.</OrangeText>
              </TextUpperCase>
            </CandleGraphText>
            <Image alt="CandleGraph" src={CandleGraph}></Image>
          </CandleGraphContainer>
          <IconList />

          <RankingContainer>
            <RankingContainerTitle>
              ACOMPANHE SUA PERFORMANCE <br /> E COMPARE COM O RESTO DO MERCADO!
            </RankingContainerTitle>
            <RankingContainerContent>
              <RankingContainerText>
                <Text>
                  Na
                  <TextBold>
                    <OrangeText> GoTradeArena </OrangeText>
                  </TextBold>
                  nós temos uma missão bem clara, que é de criar um ambiente
                  onde os traders novos ou experientes possam treinar suas
                  estratégias e conhecimentos e ainda assim poder ganhar
                  dinheiro.
                </Text>
                <Text>
                  Apos todo torneio, o participante poderá ver suas métricas de
                  L/P e Assertividade e ainda comprara com 1 participante de sua
                  escolha.
                </Text>
                <Text>
                  Assim, você poderá entender qual estratégia seu oponente usou
                  e tirar algum ensinamento de base e comparar com sua
                  estratégias.
                </Text>
              </RankingContainerText>
              <RankingImage alt="RankingMockup" src={RankingMockup} />
            </RankingContainerContent>
          </RankingContainer>

          <ModalityContainer>
            <ModalityContainerTitle>
              <TextBoldUppercase>
                <OrangeText>ESCOLHA SUA MODALIDADE</OrangeText>
              </TextBoldUppercase>
              <TextBoldUppercase>DE PREMIAÇÃO</TextBoldUppercase>
            </ModalityContainerTitle>

            <ModalityContainerContent>
              <ModalityContent>
                <ModalityTitle>
                  <BlueText>ALL WIN</BlueText>
                </ModalityTitle>

                <ModalityRow>
                  <ModalityImage alt="ModalityImageA" src={ModalityA} />
                  <ModalityText>
                    O All Win, onde 40% dos participantes são premiados com até
                    6x o valor da entrada! Ideal para quem está validando
                    estratégias!
                  </ModalityText>
                </ModalityRow>
                <ModalityTitle>
                  <GreenText>High Prize</GreenText>
                </ModalityTitle>
                <ModalityRow>
                  <ModalityImage alt="ModalityImageB" src={ModalityB} />
                  <ModalityText>
                    O HighPrize, onde 15% dos participantes são premiados em até
                    10x o valor da entrada! Para Traders mais experientes!
                  </ModalityText>
                </ModalityRow>
              </ModalityContent>

              <HomeMockupImage alt="HomeMockup" src={HomeMockup} />
            </ModalityContainerContent>
          </ModalityContainer>
        </GradientBackground>
        <StepContainer>
          <StepTitle>
            <TextBoldUppercase>
              <BlackText>PASSO A PASSO</BlackText>
            </TextBoldUppercase>
            <TextBoldUppercase>
              <WhiteText>PARA INICIAR NA ARENA</WhiteText>
            </TextBoldUppercase>
          </StepTitle>
          <Steps />
          <StepButton
            onClick={() =>
              (window.location.href =
                "https://app.arenagotrade.com/new/auth/register")
            }
          >
            PARTICIPAR AGORA
          </StepButton>
        </StepContainer>
        <About />
        <Footer />
      </MainContainer>
    );
  }
}

export default Index;
