import React from "react";
import SimulatorMockup from "../../assets/images/SimulatorMockup.png";
import TorneioMockup from "../../assets/images/TorneioMockup.png";
import Carousel from "react-bootstrap/Carousel";
import {
  CarouselItemBackground,
  Image,
  CarouselItem,
  Text,
  TextContainer,
  TextTitle,
  TextUpperCase,
  OrangeText,
} from "./SubHeaderCarousel.styles";
function SubHeaderCarousel() {
  return (
    <CarouselItemBackground>
      <Carousel controls={false} nextLabel={""} prevLabel={""} fade>
        <Carousel.Item>
          <CarouselItem>
            <Image src={SimulatorMockup} alt="SimulatorMockup" />
            <TextContainer>
              <TextTitle>Arena GOTRADE</TextTitle>
              <TextUpperCase>
                UM <OrangeText>SIMULADOR</OrangeText> <br /> QUE TRABALHA <br />{" "}
                O PSICOLÓGICO
              </TextUpperCase>
              <Text>
                A <OrangeText>GoTrade Arena</OrangeText> é uma plataforma focada
                em torneios por simulador.
              </Text>
            </TextContainer>
          </CarouselItem>
        </Carousel.Item>
        <Carousel.Item>
          <CarouselItem>
            <TextContainer>
              <TextTitle>Arena GOTRADE</TextTitle>
              <TextUpperCase>TORNEIOS</TextUpperCase>
              <Text>
                Com o intuito de oferecer uma alternativa para os traders
                trainarem suas estratégias e compararem sua perfomance com o
                resto do mercado, criamos uma dianmica de torneios onde quase
                metade dos participantes saem premiados.
              </Text>
            </TextContainer>
            <Image src={TorneioMockup} alt="TorneioMockup" />
          </CarouselItem>
        </Carousel.Item>
      </Carousel>
    </CarouselItemBackground>
  );
}

export default SubHeaderCarousel;
