import styled from "styled-components";
import Airplane from "../../assets/images/AirPlane.png";

export const Text = styled.p``;

export const TextBold = styled.span`
  font-weight: bold;
`;

export const TextUpperCase = styled.p`
  text-transform: uppercase;
`;

export const TextBoldUppercase = styled.p`
  font-weight: bold;
  text-transform: uppercase;
`;

export const MainContainer = styled.div`
  color: white;
  font-family: "Gotham", sans-serif;

  @media (max-width: 720px) {
    width: 45em;
    overflow-x: hidden;
  }
`;

export const GradientBackground = styled.div`
  background: rgb(0, 0, 0);
  background: linear-gradient(
    180deg,
    rgba(0, 0, 0, 1) 0%,
    rgba(40, 40, 40, 1) 50%,
    rgba(0, 0, 0, 1) 100%
  );
`;

export const Image = styled.img`
  width: 30em;
  height: auto;
  position: absolute;
  right: -5em;
  bottom: -1em;
  @media (max-width: 1160px) {
    display: none;
  }
`;

export const CandleGraphContainer = styled.div`
  display: flex;
  flex-direction: row;
  flex: 1;
  height: 12em;
  justify-content: flex-start;
  padding: 3em 5em;
  position: relative;
  font-size: 1.5em;
  @media (max-width: 1160px) {
    justify-content: center;
    font-size: 1.65em;
    height: 10em;
  }
`;

export const CandleGraphText = styled.div`
  width: 24em;
  line-height: 1;
  @media (max-width: 400px) {
    margin: auto;
    text-align: center;
    align-items: center;
  }
`;

export const RankingContainer = styled.div`
  display: flex;
  flex-direction: column;
  flex: 1;
  padding: 0 10em;
  @media (max-width: 1160px) {
    padding: 0 3em;
  }
`;

export const RankingContainerContent = styled.div`
  display: flex;
  flex-direction: row;
  flex: 1;
  justify-content: flex-end;
  @media (max-width: 1160px) {
    justify-content: flex-start;
    padding: 0 5em;
    align-items: center;
    flex-direction: column;
  }
`;

export const ModalityContent = styled.div`
  display: flex;
  flex-direction: column;
  flex: 1;
  align-items: left;
  text-align: left;
  padding: 4em 0;
`;

export const ModalityContainer = styled.div`
  display: flex;
  flex-direction: column;
  padding: 0em 10em;
  align-items: left;
  flex: 1;
  @media (max-width: 1160px) {
    align-items: center;
    text-align: center;
  }
`;

export const ModalityContainerContent = styled.div`
  display: flex;
  flex-direction: row;
  flex: 1;
  padding: 0 5em;
  @media (max-width: 1160px) {
    flex-direction: column;
    align-items: center;
  }
`;

export const ModalityRow = styled.div`
  display: flex;
  flex-direction: row;
  flex: 1;
  align-items: flex-end;
`;

export const ModalityContainerTitle = styled.div`
  font-size: 2em;
  line-height: 1;
`;

export const ModalityTitle = styled.p`
  font-size: 2em;
  text-transform: uppercase;
  margin-left: 2.5em;
`;

export const ModalityText = styled.p`
  font-size: 1em;
  width: 28em;
`;

export const RankingContainerText = styled.div`
  display: flex;
  flex-direction: column;
  flex: 1;
  font-size: 0.85em;
  padding: 4em 0em;
  line-height: 1.8;
  position: absolute;
  left: 18em;
  max-width: 30em;
  @media (max-width: 1160px) {
    position: inherit;
    left: 0;
    max-width: none;
    text-align: center;
  }
`;

export const RankingImage = styled.img`
  width: 44em;
  height: auto;
  @media (max-width: 1160px) {
    width: 48em;
    margin-left: 1.5em;
  }
`;

export const HomeMockupImage = styled.img`
  width: 45em;
  height: auto;
  position: absolute;
  right: -5em;
  @media (max-width: 1160px) {
    position: relative;
    right: -2em;
    width: 55em;
  }
`;

export const ModalityImage = styled.img`
  height: 3em;
  margin: 1em;
`;

export const RankingContainerTitle = styled.p`
  font-weight: bold;
  text-transform: uppercase;
  font-size: 2em;
  max-width: 25em;
  @media (max-width: 1160px) {
    max-width: none;
    text-align: center;
    margin: auto;
    font-size: 1.8em;
  }
`;

export const StepContainer = styled.div`
  display: flex;
  flex-direction: column;
  flex: 1;
  padding: 5em 5em;
  line-height: 1.8;
  background-image: linear-gradient(
      to bottom,
      rgba(229, 127, 53, 0.9),
      rgba(254, 180, 51, 0.8)
    ),
    url(${Airplane});
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
`;

export const StepTitle = styled.div`
  display: flex;
  flex-direction: column;
  flex: 1;
  font-size: 2em;
  line-height: 1;
  font-family: "Gotham Black", sans-serif;
  margin-bottom: 1.5em;
`;

export const StepButton = styled.button`
  font-size: 1em;
  font-family: "Gotham", sans-serif;
  font-weight: bold;
  background: black;
  color: white;
  text-align: center;
  margin: auto;
  padding: 0.5em 4em;
  border: none;
`;

export const OrangeText = styled.span`
  color: #ff8d17;
`;

export const WhiteText = styled.span`
  color: white;
`;

export const BlackText = styled.span`
  color: black;
`;

export const BlueText = styled.span`
  color: #24c3ee;
`;

export const GreenText = styled.span`
  color: #4fb948;
`;
