import React from "react";
import Ativo from "../../assets/icons/Ativo.svg";
import Premio from "../../assets/icons/Premio.svg";
import Entrada from "../../assets/icons/Entrada.svg";
import Duracao from "../../assets/icons/Duracao.svg";
import Saldo from "../../assets/icons/Saldo.svg";
import Modalidade from "../../assets/icons/Modalidade.svg";
import Participantes from "../../assets/icons/Participantes.svg";

import {
  MainContainer,
  Image,
  TextContainer,
  Text,
  IconItemContainer,
  SubText,
  MobileView,
} from "./IconList.styles";
function IconList() {
  return (
    <MainContainer>
      <MobileView>
        <IconItemContainer>
          <Image src={Ativo} />
          <TextContainer>
            <Text>ATIVO</Text>
            <SubText>
              WDO - WIN
              <br /> (MINI DÓLAR - MINI INDICE)
            </SubText>
          </TextContainer>
        </IconItemContainer>
        <IconItemContainer>
          <Image src={Premio} />
          <TextContainer>
            <Text>PRÊMIO</Text>
            <SubText>4 A 10X O VALOR DA ENTRADA</SubText>
          </TextContainer>
        </IconItemContainer>
      </MobileView>
      <MobileView>
        <IconItemContainer>
          <Image src={Entrada} />
          <TextContainer>
            <Text>ENTRADA</Text>
            <SubText>GRATUITOS OU À PARTIR DE R$ 20,00</SubText>
          </TextContainer>
        </IconItemContainer>
        <IconItemContainer>
          <Image src={Duracao} />
          <TextContainer>
            <Text>DURAÇÃO</Text>
            <SubText>A PARTIR DE 1H OU ATÉ O FIM DO PREGÃO</SubText>
          </TextContainer>
        </IconItemContainer>
      </MobileView>
      <MobileView>
        <IconItemContainer>
          <Image src={Saldo} />
          <TextContainer>
            <Text>SALDO INICIAL</Text>
            <SubText>
              DEFINIDO PELO ORGANIZADOR DO TORNEIO <br />
              (SALDO FICTICIO)
            </SubText>
          </TextContainer>
        </IconItemContainer>
        <IconItemContainer>
          <Image src={Modalidade} />
          <TextContainer>
            <Text>MODALIDADE</Text>
            <SubText>
              ALL WIN(40% PREMIADOS) OU HIGH PRIZE(15% PREMIADOS)
            </SubText>
          </TextContainer>
        </IconItemContainer>
        <IconItemContainer>
          <Image src={Participantes} />
          <TextContainer>
            <Text>PARTICIPANTES</Text>
            <SubText>
              DEFINIDO PELO ORGANIZADOR ATÉ NO MÁXIMO 100 PESSOAS
            </SubText>
          </TextContainer>
        </IconItemContainer>
      </MobileView>
    </MainContainer>
  );
}

export default IconList;
