import styled from "styled-components";

export const MainContainer = styled.div`
  display: flex;
  flex-direction: row;
  flex: 1;
  min-height: 10em;
`;

export const Number = styled.p`
  font-size: 1.5em;
  margin-left: 0.2em;
  width: 2em;
  height: 2em;
  font-family: "Gotham Black", sans-serif;
  font-weight: bold;
  text-align: center;
  border-radius: 50%;
  background-color: black;
  @media (max-width: 400px) {
    font-size: 1em;
  }
`;

export const TextContainer = styled.div`
  display: flex;
  flex-direction: column;
  flex: 1;
  margin-top: 1em;
  margin-left: 0.4em;
  text-align: left;
  line-height: normal;
`;

export const Text = styled.p`
  font-weight: bold;
  font-family: "Gotham Black", sans-serif;
  font-size: 1.5em;
  color: black;
  @media (max-width: 400px) {
    font-size: 1em;
  }
`;

export const SubText = styled.p`
  color: black;
  font-size: 0.8em;
  font-weight: bold;
`;

export const IconItemContainer = styled.div`
  display: flex;
  flex-direction: row;
  flex: 1;
  align-items: middle;
`;
