import styled from "styled-components";

export const MainContainer = styled.div`
  display: flex;
  flex-direction: row;
  flex: 1;
  align-items: center;
  justify-content: space-between;
  padding: 0 10em;
  font-family: "Gotham", sans-serif;
  min-height: 24em;
  @media (max-width: 400px) {
    flex-direction: column;
  }
`;

export const MobileView = styled.div`
  display: flex;
  flex-direction: row;
  flex: 1;
`;

export const Image = styled.img`
  height: 5.5em;
  width: 5.5em;
  border: 2px solid white;
  border-radius: 50%;
  margin-bottom: 2em;
  @media (max-width: 1160px) {
    height: 4.5em;
    width: 4.5em;
  }
`;

export const TextContainer = styled.div`
  display: flex;
  flex-direction: column;
  flex: 1;
  line-height: normal;
  align-items: center;
  text-align: center;
`;

export const Text = styled.p`
  font-weight: bold;
  font-size: 0.85em;
  text-align: center;
  min-width: 10em;
  @media (max-width: 1160px) {
    font-size: 0.8em;
  }
`;

export const SubText = styled.p`
  color: #f88717;
  font-size: 0.6em;
  text-align: center;
  @media (max-width: 1160px) {
    font-size: 0.65em;
  }
  width: 10em;
`;

export const IconItemContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  flex: 1;
`;
