import React from "react";
import FacebookLogo from "../../assets/icons/FacebookLogo.svg";
import InstragramLogo from "../../assets/icons/InstragramLogo.svg";
import ArenaLogo from "../../assets/icons/ArenaIcon.png";
import {
  MainContainer,
  LogosContainer,
  NavContainer,
  ButtonsContainer,
  RegisterButton,
  LoginHyperlink,
  Logos,
  Text,
  ArenaLogoImg,
} from "./Header.styles";

//import Nav from "../Nav/Nav";

function Header() {
  return (
    // <Nav />
    <MainContainer>
      <LogosContainer>
        <ArenaLogoImg alt="ArenaLogo" src={ArenaLogo} />
        <Logos alt="FacebookLogo" src={FacebookLogo} />
        <Logos alt="InstragramLogo" src={InstragramLogo} />
      </LogosContainer>

      <NavContainer>
        <Text style={{ color: "orange" }}>A Arena</Text>
        <Text>Blog</Text>
        <Text>Dúvidas</Text>
        <Text>Fale Conosco</Text>
      </NavContainer>

      <ButtonsContainer>
        <LoginHyperlink href="https://app.arenagotrade.com/new/auth/login">
          LOGIN
        </LoginHyperlink>
        <RegisterButton
          onClick={() =>
            (window.location.href =
              "https://app.arenagotrade.com/new/auth/register")
          }
        >
          CADASTRE-SE
        </RegisterButton>
      </ButtonsContainer>
    </MainContainer>
  );
}

export default Header;
